import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Executar_reuniao } from '../../../modelo/Executar_reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'executar_reuniao-dados-details',   templateUrl: 'executar_reuniao_dadosDetails.component.html' }) export class Executar_reuniaoDadosDetailsComponent implements OnInit {    executar_reuniao: Executar_reuniao;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.executar_reuniao.Executar_reuniao";                 let id = +params['id'];         this.executar_reuniao = new Executar_reuniao();         this.executar_reuniao.cd_executar_reuniao = id;         o.objetoJson = this.executar_reuniao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a executar_reuniao. '; }      this.executar_reuniao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


